import React , {useState, useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
// `import {} from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';
import './SearchDetail.scss';
import Data from './Item.js';

function SearchDetail(props) {
    console.log(props.item[0])
    console.log(props.apechList)
    console.log(props.lionList)
return (
    <div className="searchDetail">
        {
            props.apechList.length>0 ? 
            <div className="searchDetailBanner apechBanner"> </div>
            : <div className="searchDetailBanner lionBanner"> </div>
        }
        {
            props.apechList.length > 0? 
            <div className="gridBox">
            {props.apechList.map((a,i)=>{
                return (
                    <div className="searchDetItem">
                        <div className="itemImage">
                            <img src={`/img/data${props.apechList[i].id}.jpg`}></img>
                        </div>
                        <div className="itemInfo">
                            <p>{props.apechList[i].title}</p>
                            <p>{props.apechList[i].price}원</p>
                        </div>
                </div>
                )
            })}

        </div>
        :
        <div className="gridBox">
        {props.lionList.map((a,i)=>{
            return (
                <div className="searchDetItem">
                    <div className="itemImage">
                        <img src={`/img/data${props.lionList[i].id}.jpg`}></img>
                    </div>
                    <div className="itemInfo">
                        <p>{props.lionList[i].title}</p>
                        <p>{props.lionList[i].price}원</p>
                    </div>
            </div>
            )
        })}

    </div>
        
        }

    </div>
)

}

export default SearchDetail