import React , {useState, useEffect} from 'react';
import './Detail.scss';
import Data from './Item.js';
import LikeLists from './LikeList.js';
import { useHistory, useParams } from 'react-router-dom';
import {Nav,Button} from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';
import CartList from './CartList';

function Detail(props) {
    let history = useHistory();
    let {id} = useParams();
    let [tabNum, tabNumChange] = useState(0);
    let [inputVal, inputValChange] = useState('');
    let [opaSwitch, opaSwitchChange] = useState(false);

    let [likeAlert, likeAlertChange] = useState(false);
    let [likeAlertOpa, likeAlertOpaChange] = useState(false)
    let [likeDelAlert, likeDelAlertChange] = useState(false);
    let [likeDelAlertOpa, likeDelAlertOpaChange] = useState(false);

    let [cartAlert, cartAlertChange] = useState(false);
    let [cartAlertOpa, cartAlertOpaChange] = useState(false)

    let [cartClicked, cartClickedChange] = useState(false)

    // console.log(props.LikeListsSi.length+'길~~이')

    let paramId = props.item.find((a)=>{
        return a.id == id;
    })

    let [paramIdCopy,paramIdCopyChange] = useState(paramId)
    // console.log(paramIdCopy)
    // console.log(paramId)



    let [paramReview,paramReviewChange] = useState([...paramId.review])
    // console.log(paramReview)


    return (
        <div className="detailContainer">
            <div className="detailIMG">
                <img src={`/img/data${id}.jpg`}></img>
            </div>
            <div className="detailTitle">
                <h3>{paramId.title}</h3>
                <div className="price">
                    <span className="moto">{paramId.motoPrice}원</span>
                    <span className="sale">{paramId.motoPrice - paramId.motoPrice * 20 / 100}원</span>
                </div>
                <div>
                {
                paramId.heart == false ? <span>🖤</span> : <span>🧡</span>
                } : {paramId.likes}</div>
                <button className="btn btn-danger"
                onClick = {()=>{history.push('/')}}>뒤로가기</button> 
                <button className="btn btn-danger" onClick={()=>{
                    paramId.heart == false ? paramId.heart = true : paramId.heart = false;
                    // console.log(paramId.heart)
                    if (paramId.heart == false ) {
                        let likeListSiC = [...props.LikeListsSi];
                        likeListSiC.splice(paramId, 1)
                        props.LikeListsSiChange(likeListSiC)   
                        console.log(paramId.heart);
                        console.log(paramId)
                        paramId.likes-=1; 
                        paramIdCopyChange(paramId.likes);
                        likeDelAlertChange(true)
                        likeDelAlertOpaChange(true)
                    } if(paramId.heart == true ) {
                        let likeListSiC = [...props.LikeListsSi];
                        likeListSiC.push(paramId)
                        props.LikeListsSiChange(likeListSiC)  
                        // console.log(props.LikeListsSi)   
                        console.log(paramId.heart);
                        console.log(paramId)  
                        paramId.likes+=1; 
                        paramIdCopyChange(paramId.likes);
                        likeAlertChange(true);
                        likeAlertOpaChange(true);
                    }
                }}>찜하기</button> 
            </div>
            <div className="orderFixed" onClick={()=>{
                let CartListSiC = [...props.CartListSi];
                CartListSiC.push(paramId);
                props.CartListSiChange(CartListSiC);
                console.log(props.CartListSi);
                cartAlertChange(true);
                cartAlertOpaChange(true);
                cartClickedChange(true)
                console.log(cartClicked)
            }}>
            장바구니 담기
            </div>
            <Nav fill variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{
                        tabNumChange(0); opaSwitchChange(false)
                    }}>제품 상세</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{
                        tabNumChange(1); opaSwitchChange(false)
                    }}>리뷰</Nav.Link>
                </Nav.Item>
            </Nav>
            <CSSTransition in={opaSwitch} className="darara" timeout={500}>
            <TabCompo tabNum={tabNum} tabNumChange={tabNumChange}  paramId={paramId}
            inputValChange={inputValChange} inputVal={inputVal}
            paramReview={paramReview} paramReviewChange={paramReviewChange}
            opaSwitchChange={opaSwitchChange}/>
            </CSSTransition>
            {
            likeAlert == true ?
                <CSSTransition in={likeAlertOpa} className="darara" timeout={3000}>
                    <LikeAlert likeAlertOpaChange={likeAlertOpaChange} likeAlertChange={likeAlertChange}></LikeAlert>
                </CSSTransition>
                : null
            }
            {
            likeDelAlert == true ?
                <CSSTransition in={likeDelAlertOpa} className="darara" timeout={3000}>
                    <LikeDelAlert likeDelAlertOpaChange={likeDelAlertOpaChange} likeDelAlertChange={likeDelAlertChange}></LikeDelAlert>
                </CSSTransition>
                :null
            }
            {
            cartAlert == true ?
                <CSSTransition in={cartAlertOpa} className="darara" timeout={3000}>
                    <CartAlert cartAlertOpaChange={cartAlertOpaChange} cartAlertChange={cartAlertChange}></CartAlert>
                </CSSTransition>
                : null
            }

      </div>
    )
}

function TabCompo(props) {
    useEffect(()=> {
        props.opaSwitchChange(true);
       
    })
    if (props.tabNum == 0) {
        return (
            <div className="content">
                <div className = "contentTop">
                이 상품의 이름은 '{props.paramId.title}' 입니다. <br></br>
                가격은 {props.paramId.motoPrice}원입니다.<br></br>
                20% 할인행사중이니 현재가격 '{props.paramId.motoPrice - props.paramId.motoPrice * 20 / 100}원'입니다.<br></br>
                총 {props.paramId.likes}명이 이 상품을 찜했습니다.
                </div>
                <div className="blabla">
                우리 귀여운 {props.paramId.korName} 좀 보고 가세요!<br></br>
                {props.paramId.korName}! 너와 함께라면 무엇이든 할 수 있어!
                </div>
                <div className="contentIMG">
                    <img src={`/img/${props.paramId.char}1detail.jpg`}></img>
                </div>
                <div className="blabla">
                스트레스가 쌓일때는 {props.paramId.korName}친구를 안아주세요!<br></br>
                {props.paramId.korName}! 지금 안사면 후회할걸?!
                <div className="contentIMG">
                    <img src={`/img/${props.paramId.char}2detail.jpg`}></img>
                </div>
                <div className="contentIMG">
                    <img src={`/img/${props.paramId.char}3detail.jpg`}></img>
                </div>
                </div>
            </div>
        )
    }
    if (props.tabNum == 1) {
        return (
            <div>
            <ul className="review">
            {
             props.paramId.review.map(function(a,i) {
                return (
                <li>
                    <span>" {props.paramId.review[i]} "</span>
                    <div>2021.08.02</div>
                </li>
                )
            })
}
            </ul>
            <div className="reviewWrite">
                <input onChange={(e)=>{props.inputValChange(e.target.value)}}/>
                <Button variant="warning" onClick={()=>{
                 props.paramId.review.unshift(props.inputVal);
                 props.paramReviewChange(props.paramId.review)
                }}>리뷰등록하기</Button>
            </div> 

            </div>
        )
    }
}

function LikeAlert(props) {
    useEffect(()=>{
        return setTimeout(()=>{
            props.likeAlertChange(false);
        },650)
    })
    return (
        <div className="likeAlert">
            <span>찜목록에 추가 되었습니다.</span>
        </div>
    )
}

function LikeDelAlert(props) {
    useEffect(()=>{
        return setTimeout(()=>{
            props.likeDelAlertChange(false);
        },650)
    })
    return (
        <div className="likeAlert">
            <span>찜목록에서 삭제 되었습니다.</span>
        </div>
    )
}

function CartAlert(props) {
    useEffect(()=>{
        return setTimeout(()=>{
            props.cartAlertChange(false);
        },650)
    })
    return (
        <div className="likeAlert">
            <span>장바구니에 추가 되었습니다.</span>
        </div>
    )
}

export default Detail;