  /*eslint-disable*/
  import React, {useState, useEffect, useRef} from 'react';
  import './App.scss';
  import {Navbar, Container, Nav, Button, Table} from 'react-bootstrap'
  import {Link, Route, Switch} from 'react-router-dom'
  import Data from './Item.js';
  import LikeLists from './LikeList.js';
  import CartList from './CartList.js';
  import Detail from './Detail.js';
  import Cart from './Cart.js';
  import Search from './Search.js';
  import LikePage from './LikePage.js'
  import SearchDetail from './SearchDetail.js'
  import { useHistory} from 'react-router-dom';
  import styled from 'styled-components'; 



  
  function App() {
    let [item, itemChange] = useState(Data);
    let [moreClick, moreClickChange] = useState(0);
    let [isClick, isClickChange] = useState(false);
    let num = useRef(0);
    let [LikeListsSi,LikeListsSiChange] = useState(LikeLists);
    let [CartListSi,CartListSiChange] = useState(CartList);

    let [apechList,apechListChange] = useState([]);
    let [lionList,lionListChange] = useState([]);


    useEffect(()=>{
      moreClick == 0 ? console.log('gg'):''
      moreClick === 1 ? console.log('1212') : ''
  })

  let GridContainer = styled.div`
  display: grid;
  width: 640px;
  margin: 0 auto;
  grid-template-rows: repeat(${6*moreClick+6}, 100px);
  grid-template-columns: repeat(6, 2fr);
  @media all and (min-width:640px) and (max-width:779px) {
  width: 100%;
  }
  @media all and (max-width:479px) {
    width: 100%;
    grid-template-rows: repeat(${6*moreClick+6}, 50px);
  }
  @media all and (min-width:480px) and (max-width:639px) {
    width: 100%;
    grid-template-rows: repeat(${6*moreClick+6}, 90px);
  }
  `


    return (
      <div className="App">
        <>
          <Navbar variant="dark" className="navBar">
            <Container className="navCon">
            <Navbar.Brand className="Brand"><Link to="/">jiyu React</Link></Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link className="navLink"><Link to="/">홈</Link></Nav.Link>
              <Nav.Link className="navLink"><Link to="/cart">장바구니</Link></Nav.Link>
              <Nav.Link className="navLink"><Link to="/likes">찜목록</Link></Nav.Link>
              <Nav.Link className="navLink"><Link to="/search">검색</Link></Nav.Link>
            </Nav>
            </Container>
          </Navbar>
        </>
        <Link to="/cart"><div class="fixed">🛒<span>{CartListSi.length}</span></div></Link>
        <Link to="/likes"><div class="fixed fixedLike">💛<span>{LikeListsSi.length}</span></div></Link>

  
  <Switch>
  <Route exact path="/">
        <div className="jumbo">
            <h3>React KAKAO</h3>
            <h5>WELCOME 20% SALE</h5>
        </div>
      <GridContainer className="gridContainer">
      {
        item.map((a,i)=> {
          if(i < 6){
            return (
            <MainItems item={item[i]} i={i} isClickChange={isClickChange}></MainItems>
            )
          }
          if (moreClick==1 && i<12) {
          
            return (
              <MainItems item={item[i]} i={i} isClickChange={isClickChange}></MainItems>
              )
          }
          if (moreClick==2 && i<18) {
         
            return (
              <MainItems item={item[i]} i={i} isClickChange={isClickChange}></MainItems>
              )
          }
          if (moreClick==3 && i<24) {
           
            return (
              <MainItems item={item[i]} i={i} isClickChange={isClickChange}></MainItems>
              )
          }
        
        
        })
        
      }
      </GridContainer>

      {
        moreClick <= 2
        ?   <Button variant="outline-warning" onClick={()=>{
          moreClickChange(moreClick+=1);
          console.log(moreClick);
          let gridContainer = document.querySelector('.gridContainer');
          gridContainer.style.gridTemplateRows = `repeat(${6*(moreClick+1)}, 100px)`;
        }}>더보기</Button> : null
      }
  </Route>
  
  <Route path="/detail/:id">
      <Detail item={item}  LikeListsSi={LikeListsSi} LikeListsSiChange={LikeListsSiChange}
      CartListSi={CartListSi} CartListSiChange={CartListSiChange}/>
  </Route>

  <Route path="/cart">
    <Cart item={item}  LikeListsSi={LikeListsSi} LikeListsSiChange={LikeListsSiChange}
    CartListSi={CartListSi} CartListSiChange={CartListSiChange}/>
  </Route>

  <Route path="/likes">
    <LikePage LikeListsSi={LikeListsSi} LikeListsSiChange={LikeListsSiChange}
    CartListSi={CartListSi} CartListSiChange={CartListSiChange}/>
  </Route>

  <Route path="/search">
    <Search LikeListsSi={LikeListsSi} LikeListsSiChange={LikeListsSiChange}
    CartListSi={CartListSi} CartListSiChange={CartListSiChange} item={item}  itemChange={itemChange}
    apechList={apechList} apechListChange={apechListChange} lionList={lionList} lionListChange={lionListChange}/>
  </Route>

  <Route path="/search_detail">
  <SearchDetail apechList={apechList} item={item}  itemChange={itemChange} apechListChange={apechListChange} lionList={lionList} lionListChange={lionListChange}/>
  </Route>

  </Switch>
      <footer>
        이 페이지는 React.js로 제작된 김지유의 포트폴리오용<br/>카카오프렌즈 카피 사이트입니다.
        <br/> 현재 개발중에 있습니다. <br/> ( 검색(데이터필터) 기능, 장바구니 기능 개선 및 <br/>스타일추가 작업중 )
      </footer>
  </div>
    );
  }
  function MainItems(props) {
    let history = useHistory();
  return (
      <div className={`item${props.i+1} item`} onMouseEnter ={()=>{
        let hoverBox = document.querySelectorAll('.hoverBox');
        // console.log(hoverBox[props.i])
        hoverBox[props.i].style.top = '85%';
      }} onMouseLeave ={()=>{
        let hoverBox = document.querySelectorAll('.hoverBox');
        hoverBox[props.i].style.top = '150%';
      }} onClick = {()=>{
        props.isClickChange(true)
      }}>
        <div className="itemIMG" onClick={()=>{
          history.push('/detail/' + props.item.id)
        }}></div>
        <div className="hoverBox">
          <span>{props.item.title}</span>
        </div>
      </div>
  )
  }
  
  
  export default App;
  // export default () => <FontAwesomeIcon icon={faCamera} />