import React , {useState, useEffect} from 'react';
import './Cart.scss';
import { useHistory, useParams } from 'react-router-dom';
// `import {} from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';

function Cart(props) {
    const totalPrice = props.CartListSi.reduce((prev,curr)=>{
        return prev+curr.price;
    },0);
    const totalPriceUnder = totalPrice+2500;
    let CartCopy = [];
    let array = [];

    let [cart, cartChange] = useState(props.CartListSi);
    // console.log(cart)

    let goTrash = [];
    let [trash, trashChange] = useState(goTrash)


return (
    <div className="cart">
    <h4 className="title">장바구니</h4>
    {
        props.CartListSi.length > 0 ? 
        <img className="trash" src="img/delete-4_90871.png" onClick={()=>{
            console.log(trash);
            let hey =[];
            props.CartListSi.map(id=>{
                hey.push({
                    id: trash.filter(x=>x.id === id.id)
                })
            })
            let here = JSON.stringify(hey)
            console.log(here)
            here = [...trash]
            console.log(trash);
            console.log(props.CartListSi)
            // props.CartListSiChange(trash);
            // console.log(cart);
            let checkDel = props.CartListSi.filter(x=> !trash.includes(x))
            console.log(checkDel)
            props.CartListSiChange(checkDel);
        }}></img> : null
    }
    <ul>
    {
        props.CartListSi.length > 0 ? props.CartListSi.map((a,i)=>{
            let moneyMoto = props.CartListSi[i].price
            console.log(moneyMoto)
            return (
            <li>
            <input type="checkbox" name="color" value="blue"
                onClick={(event)=>{
                    console.log(event.target.checked);
                    if (event.target.checked == true) {
                        trash.push(props.CartListSi[i])
                        var trashBox = trash.filter((clean)=>{
                            return clean.id === props.CartListSi[i].id
                        })
                        trashBox = [...trash]
                        console.log(trashBox)
                        trashChange(trash)
                    }
                    if (event.target.checked == false) {
                        trash.splice(props.CartListSi[i],1)
                        console.log(trash)
                        trashChange(trash)
                    }
                }}/>
                <img src={`/img/data${props.CartListSi[i].id}.jpg`}></img>
                <div className="cartContent">
                    <p>{props.CartListSi[i].title}</p>
                    <p>{props.CartListSi[i].price - props.CartListSi[i].price * 20 / 100}원</p>
                    <p>
                        <span onClick={()=>{
                            if (props.CartListSi[i].quan > 1) {
                                let quanCopy = [...props.CartListSi];
                                quanCopy[i].quan--
                                quanCopy[i].price -= quanCopy[i].motoPrice;
                                cartChange(quanCopy);
                                trashChange(quanCopy);
                                console.log(trash)
                            }
                        }}>-</span>
                        <span>{props.CartListSi[i].quan}개</span>
                        <span onClick={()=>{
                            let quanCopy = [...props.CartListSi];
                            quanCopy[i].quan++
                            quanCopy[i].price += quanCopy[i].motoPrice;
                            cartChange(quanCopy)
                            trashChange(quanCopy);
                            console.log(trash)
                        }}>+</span>
                    </p>
                </div>
            </li>
            )
        }) : <div className="ifDefault">장바구니에 담긴 상품이 없습니다<p>담아주세요!</p>
            <img src="/img/please.png"></img></div>
    } 
        </ul>

    {
        props.CartListSi.length > 0 ? 
        <div className="bill">
            <p>
                <span>총 주문금액</span>
                <span>{totalPrice}원</span>
            </p>
            <p>
                <span>배송비</span>
                <span>{
                    totalPrice >= 80000 ? <span>무료</span>:<span>2500원<p className="free">80,000원 이상 주문시 무료</p></span>
                }</span>
            </p>
            <p>
                <span>총 결제금액</span>
                <span>{
                    totalPrice >= 80000 ? <span>{totalPrice}원</span>:<span>{totalPriceUnder}원</span>
                }</span>
            </p>
        </div>
        :null
    }
    {
    props.CartListSi.length > 0 ?
        <span className="orderFix">
            {
                totalPrice >= 80000 ? <span>{totalPrice}원 주문하기</span>:<span>{totalPriceUnder}원 주문하기</span>
            }</span> 
        : null
    } 
        
    </div>
)
}

export default Cart