import React , {useState, useEffect} from 'react';
import './Search.scss';
import { useHistory, useParams } from 'react-router-dom';
// `import {} from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';
import {Link, Route, Switch} from 'react-router-dom';
import SearchDetail from './SearchDetail.js'


function Search(props) {

    console.log(props.apechList)

    return (
        <Switch>
            <Route path="/search">
                <div className="search">
                    <input className="inputs"/>
                    <div className="charList">
                        <Link to="/search_detail"><div className="char apech" onClick={()=>{
                                console.log(props.item);
                                function isApech(x) {
                                    if(x.char == 'apech') {
                                        return true;
                                    }
                                }
                                props.apechListChange(props.item.filter(isApech))
                                console.log(props.apechList);
                                props.lionListChange([])
                        }}></div></Link>
                        <Link to="/search_detail"><div className="char lion" onClick={()=>{
                            function isLion(x){
                                if(x.char == 'lion') {
                                    return true;
                                }
                            }
                            props.lionListChange(props.item.filter(isLion))
                            console.log(props.lionList);
                            props.apechListChange([])
                        }}>
                            </div></Link>
                    </div>
                </div>
            </Route>

        </Switch>
 
    )
}


export default Search